.btn {
  font-size: rem(14);
  line-height: rem(14);
  font-weight: 700;
  padding: rem(14) rem(30) rem(14);
  border-radius: 4px;
  letter-spacing: 1px;
  text-align: center;
  display: inline-block;
  text-transform: uppercase;
  @include transition(all 0.3s linear);
  @include media-breakpoint-up(md) {
    white-space: nowrap;
  }
}

.btn-outline-light {
  padding: rem(16) rem(30);

  &:focus,
  &:hover {
    border-color: $white;
    background-color: $white;
    color: $medium-gray;
  }
}

.btn-sm {
  color: $white;
  padding: rem(11) rem(30);
}

.btn-primary {
  @extend .btn;
  background-color: $primary;
  border: 2px solid $primary;
  color: rgba($white, 0.9);
  &:hover,
  &:focus {
    background-color: $dark-yellow;
    border-color: $dark-yellow;
    color: rgba($white, 0.9);
    box-shadow: none !important;
  }  
}
.btn-primary:not(:disabled):not(.disabled):active,
.btn-primary:not(:disabled):not(.disabled):active:focus  {
  box-shadow: none !important;
}

.btn-alt {
  @extend .btn;
  background-color: $secondary;
  border: 2px solid $secondary;
  color: $white;
  &:hover,
  &:focus {
    border-color: $secondary;
    background-color: transparent;
    color: $secondary;
    box-shadow: none !important;
  }
}
.btn-alt:not(:disabled):not(.disabled):active,
.btn-alt:not(:disabled):not(.disabled):active:focus  {
  box-shadow: none !important;
}


.btn-alt2 {
  display: inline-block;
  padding: rem(10) rem(30);
  border: 2px solid $blue;
  background-color: transparent;
  color: $blue;
  &:focus,
  &:hover {
    border-color: $primary;
    background-color: $primary;
    color: $white;
  }
}

.btn-on-color {
  @extend .btn;
  border: 2px solid $primary;
  background-color: $primary;
  color: $white;
  &:focus,
  &:hover {
    border-color: $white;
    background-color: transparent;
    color: $white;
  }
}

.btn-alt-on-color {
  @extend .btn;
  border: 2px solid #BABABA;
  background-color: transparent;
  color: $white;
  &:focus,
  &:hover {
    border-color: $white;
    background-color: $white;
    color: $secondary;
  }
}

.btn-wrap {
  margin-bottom: rem(25);
  @include display(flex);
  @include flex-wrap(wrap);
  @include align-items(center);
  > button,
  > a {
    margin: 0 rem(10) rem(20) 0;
  }
}

.btn-bg {
  width: rem(330);
  /*height: rem(88);*/
  max-width: 100%;
  background-color: $black;
  padding: rem(12) 0;
  display: inline-block;
  text-align: center;
  margin-bottom: rem(25);
  vertical-align: top;
  &.red-btn-bg {
    background-color: $medium-gray;
  }
  &:nth-of-type(2) {
    padding-top: rem(19);
  }
  @include media-breakpoint-up(lg) {
    margin: 0 rem(15) rem(20) 0;
  }
}

.btn-success,
.btn-info {
  color: $black;
  &:hover,
  &:focus {
    color: $black;
  }
}

