/*================ Header styling ========================*/
.site-header {

	@include media-breakpoint-down(md){
		overflow-x: hidden;
	}

	@include media-breakpoint-up(mq){
		.navbar-collapse {
			flex-grow: 0;
		}
	}
}

.sh-sticky-wrap.stuck {
box-shadow: 0px 3px 6px #00000029;
  @include media-breakpoint-up(md) {
	  width: 100%;
	  position: fixed;
	  z-index: 99;
	  top: 0;
	  left: 0;
	  background-color: rgba($white, 0.9);
	  box-shadow: 0px 3px 6px #00000029;
	  transition: none;
  }
}

.custom-logo-link {
	@include media-breakpoint-down(md) {
		max-width: 50%;
	}
}

.site-logo,
.navbar-brand {
	@include media-breakpoint-down(sm) {
		margin: 0 auto rem(15);
	}
}

.site-logo {
	max-width: rem(400);
}

.utility-nav {
	display: flex;
	align-items: center;
	justify-content: center;
	@include media-breakpoint-up(lg) {
		order: 2;
	}
}

.sh-ph,
.sh-email {
	margin-right: rem(16);
	font-size: rem(16);
	color: white;
	i,
	.material-icons {
		font-size: rem(24);
		color: white;
		vertical-align: middle;
	}

	a {
		display: inline-block;
	}

	span:not(.material-icons) {
		display: none;
	}
}

.sh-ph a {
	margin-top: rem(3);
}

.social-icons {
  a {
    display: inline-block;
    color: $primary;
    font-size: rem(20);
    text-align: center;
    text-decoration: none;
    @include transition(opacity .2s);
    vertical-align: middle;

    i,
	.material-icons {
      display: inline-block;
      vertical-align: middle;
    }

    &:not(:last-child) {
    	margin: 0 rem(15) 0 0 !important;
    }

    &:hover {
      opacity: .9;
    }
  }

  li {
    padding: rem(5) 0;
  }
}

.search-form-tigger {
	.material-icons {
		vertical-align: middle;
	}
}

.btn-alt2.sh-contact-cta {
	padding: rem(15) rem(30);
	@include media-breakpoint-up(md) {
		margin-right: rem(15) !important;
	}
}

.header0 {
	position: relative;
  	z-index: 9;
	background-color: rgba($white, 0.9);

	.search-form-tigger {
		color: $primary;
		.material-icons {
			vertical-align: middle;
		}
		&:focus,
		&:hover {
			color: $primary;
			opacity: 0.8;
		}

		@include media-breakpoint-up(lg) {
			color: $nav-color;
			font-size: rem(25);
			&:hover,
			&:focus {
				color: $nav-hover-color;
			}
		}
	}

	.brand {
		width: rem(200);
		height: rem(90);
		display: table-cell;
		vertical-align: middle;
		a {
			display: block;
		}
	}

	.utility-nav {
		order: 0;
	}

	.site-logo {
		@media screen and (min-width:992px) and (max-width:1199px) {
			max-width: rem(330);
		}
	}

	.navbar-expand-lg {
		padding: 16px 0 21px;
		> .container {
			flex-wrap: nowrap;
			@include media-breakpoint-up(lg) {
				width: 95%;
			}
			@include media-breakpoint-up(xl) {
				width: 90%;
			}
		}

		.nav-link {
			color: #babfc4 !important;
			font-size: $nav-font-size-mobile;
			.material-icons {
				vertical-align: text-bottom;
				font-size: 18px;
			}
		}
		.menu-item-2688 {
			.nav-link::before {
				width: calc(100% - 21px) !important;
			}
		}

		.navbar-toggler {
			border: 0;
			padding: 0;
			.material-icons {
				font-size: 30px;
				color: $primary;
			}
		}

		@include media-breakpoint-up(lg) {
			.nav-link {
				color: $primary !important;
				font-size: $nav-font-size;
			}
		}
	}

	@include media-breakpoint-down(sm) {
		.site-logo,
		.navbar-brand {
			margin: 0;
		}
  	}
	@include media-breakpoint-up(md) {
		.btn-on-color {
			&:hover,
			&:focus {
				background-color: transparent;
				border-color: $primary;
				color: $primary;
			}
		}
	}
	@include media-breakpoint-up(lg) {
		.dropdown {
			.dropdown-menu {
				box-shadow: 0px 3px 6px #00000065;
				padding-top: 0;
			}
		}
		.header-inner {
			.navbar-expand-lg  {
				min-height: rem(86);
				.main-menu {
					display: inline-block;
				}
				.navbar-nav {
					> .menu-item {
						position: relative;
						margin-right: rem(10);
						.nav-link {
							padding-right: 0;
							padding-left: 0;
							line-height: rem(24);
							&::after {
								display: none;
							}
							&::before {
								content: '';
								position: absolute;
								width: 100%;
								height: 2px;
								background-color: $primary;
								display: block;
								left: 0;
								border-radius: 0px;
								bottom: 8px;
								opacity: 0;
								z-index: 1;
							}
							&:hover,
							&:focus {
								&::before {
									opacity: 1;
								}
							}
						}

						.dropdown-menu {
							li:not(:last-child) .dropdown-item {
								border-bottom: 1px solid $light-gray;
							}
						}
					}
				}
			}
		}
	}
}


.header1 {
	
	.site-logo {
		margin: 0;
	}

	.m-subnav-arrow:after {
	    color: $white;
	}

	.dropdown-item {
		color: $black;
		background-color: $primary;
		@include media-breakpoint-up(lg) {
			color: $nav-subnav-color;
			background-color: $nav-subnav-bg-color;
		}
	}

	@include media-breakpoint-up(md) {
		.navbar-expand-md .navbar-toggler {
			display: inline-block;
		}

		.navbar-expand-md .navbar-collapse {
			display: none !important;
		}
	}

	@include media-breakpoint-up(lg) {
		.utility-nav {
			@include order(2);
		}

		.navbar-expand-md .navbar-collapse {
			display: -webkit-box!important;
			display: -ms-flexbox!important;
			display: flex!important;
			-ms-flex-preferred-size: auto;
			flex-basis: auto;
		}

		.navbar-expand-md .navbar-toggler {
			display: none;
		}
	}
}

.header2 {
	@include media-breakpoint-down(md) {
		.site-logo,
		.custom-logo-link {
		    margin: 0 auto;
		}

		.utility-nav {
			width: 100%;
    		justify-content: space-between;
		}
	}
}

.header4 {
	.custom-logo-link {
		margin: 0 auto;
	}
	.top-line {
		padding: rem(15) 0;
		i,
		.material-icons {
			color: $primary;
			vertical-align: middle;
		}
	}

	.dropdown .dropdown-menu { 
		.m-subnav-arrow::after {
			color: $black;
		}
	}

	@include media-breakpoint-up(md) {
		.custom-logo-link {
			margin: 0 1rem 0 0;
		}
		.navbar-expand-md .navbar-toggler {
			display: inline-block;
		}

		.navbar-expand-md .navbar-collapse {
			display: none !important;
		}
	}

	@include media-breakpoint-up(lg) {
		.navbar-expand-md .navbar-collapse {
			display: -webkit-box!important;
	    display: -ms-flexbox!important;
	    display: flex!important;
	    -ms-flex-preferred-size: auto;
	    flex-basis: auto;
		}

		.navbar-expand-md .navbar-toggler {
			display: none;
		}

		.sh-email span,
		.sh-ph span {
	    	display: inline-block;
	    	vertical-align: middle;
		}
	}
}

.header5 {
	.top-line {
		background-color: $light-gray;
	}

	.sh-email a,
	.sh-ph a {
		color: $black;
		text-decoration: none;

		i,
		.material-icons {
			color: $primary;
		}

		span {
			display: inline-block;
			vertical-align: middle;
		}

		&:hover {
			color: $blue;

			i,
			.material-icons {
				color: $blue;
			}
		}
	}

	.sh-email:hover {
		span {
			text-decoration: underline;
		}
	}

	.search-form-tigger {
		@include media-breakpoint-up(lg) {
			color: $nav-color;
			&:hover,
			&:focus {
				color: $nav-hover-color;
			}
		}
	}

	.navbar-light .navbar-toggler,
	.search-form-tigger,
	.social-icons a {
		color: $primary;
		border: none;
		&:hover {
			color: $blue;
		}
	}

	.social-icons {
		height: 100%;
		padding: rem(5) 0;
	    display: flex;
	    align-items: center;
	}

	.sh-search {
		margin-bottom: rem(3);
	}

	@include media-breakpoint-down(md) {
		.sh-email a span,
		.sh-ph a span {
			display: none;
		}

		.utility-nav {
			width: 100%;
			justify-content: space-between;
		}

		.sh-logo-wrap {
			width: 100%;
			text-align: center;
			margin-bottom: rem(20);
		}

		.sh-search {
			margin-bottom: 0;
		}
	}
}

.header6 {
	@include media-breakpoint-up(md) {
		.header-inner {
			.container {
				flex-wrap: nowrap;
			}
		}

		.sh-email, .sh-ph {
			span:not(.material-icons){
				display: inline-block;
				vertical-align: middle;
			}
		}

		.utility-nav {
			margin-left: rem(30);
		}
	}

	@include media-breakpoint-up(lg) {
		.utility-nav {
			margin-left: 0;
		}
	}

	@media (max-width: 450px) {
		.sh-rfq-mob-cta,
		.sh-contact-mob-cta {
			padding-left: rem(10);
			padding-right: rem(10);
		}
	}
}

.header7 {
	position: relative;
	z-index: 9;

	.header-topline-menu {
		.navbar-nav {
			line-height: rem(23);
			.nav-link {
				color: $white;
				font-weight: 400;
				line-height: rem(28);
				&:hover,
				&:focus {
					color: $secondary;
				}
			}
			@include media-breakpoint-up(lg) {
				.nav-link {
					color: $gray;
					&:hover,
					&:focus {
						color: $secondary;
					}
				}
			}
		}
	}
	.top-line {
		> .container {
			padding-top: rem(10);
			padding-bottom: rem(10);
		}

		.sh-ph,
		.sh-email,
		.sh-search {
			line-height: rem(22);
			vertical-align: text-bottom;
			a {
				color: $gray;
				margin-top: 0;
				.material-icons {
					color: $primary;
				}
				&:hover,
				&:focus {
					color: $secondary;
					.material-icons {
						color: $secondary;
					}
				}
			}
		}
		@include media-breakpoint-down(sm) {
			.container {
				padding-top: 0;
				padding-bottom: 0;
			}
			.text-right {
				display: flex;
				align-items: center;
				justify-content: space-between;
			}
			.btn-primary {
				border-radius: 0;
			}
		}
	}
	.site-logo {
		max-width: rem(350);
		margin-left: 0;
		margin-bottom: 0;
		&.middle-site-logo {
			display: none;
		}
		@media screen and (max-width: 375px) {
			margin-right: rem(10);
		}
	}

	.sh-sticky-wrap  {
		background-color: rgba($black, 0.4);
		.navbar-expand-lg {
			min-height: 110px;
			> .container {
				flex-wrap: nowrap;
			}
			.dropdown-menu {
				box-shadow: 0px 3px 6px #00000089;
				padding-top: 0;
				li {
					.dropdown-item {
						border-bottom: 1px solid $lighter-gray;
					}
					&:last-child {
						.dropdown-item {
							border-bottom: 0;
						}
					}
					&.hashlink {
						a {
							background-color: $white;
							color: $gray;
						}
					}
				}
			}
		}
		&.stuck {
			background-color: $black;
		}
	}
	@include media-breakpoint-down(md) {
		.navbar-dark {
			.site-nav-container {
				background-color: $black;
			}
			.left-main-menu {
				padding-top: rem(22);
			}
			.dropdown-menu {
				li {
					.m-subnav-arrow  {
						&::after {
							color: $black;
						}
						&.active1 {
							&::after {
								color: $white;
							}
						}
					}
				}
			}
		}
	}
	@include media-breakpoint-up(md) {
		.top-line {
			.sh-email {
				span:not(.material-icons) {
					display: none;
				}
			}
			.sh-email,
			.sh-email,
			.sh-ph {
				margin-right: rem(24);
				.material-icons {
					font-size: rem(20);
				}
			}
		}
		.header-inner {
			.container {
				flex-wrap: nowrap;
			}
		}

		.sh-email, .sh-ph {
			span:not(.material-icons){
				display: inline-block;
				vertical-align: middle;
			}
		}

		.utility-nav {
			margin-left: rem(30);
			.navbar-toggler {
				margin-right: rem(30);
			}
		}
	}

	@include media-breakpoint-up(lg) {
		.top-line {
			.sh-search {
				margin-right: 0 !important;
			}
		}
		.site-logo {
			display: none;
			&.middle-site-logo {
				display: inline-block;
			}
		}

		.site-nav-container {
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: space-between;
			background-color: transparent !important;
		}
		.header-topline-menu {
			.navbar-nav {
				.nav-link {
					padding-left: 0;
				}
			}
		}
		.header-inner {
			.navbar-expand-lg  {
				padding-top: rem(26);
				padding-bottom: rem(25);
				.navbar-nav {
					> .menu-item {
						position: relative;
						.nav-link {
							padding-right: 0;
							padding-left: 0;
							&::after {
								display: none;
							}
						}
						&:not(:last-child) {
							margin-right: rem(15);
						}
					}
				}
			}
		}
		.left-main-menu {
			margin-right: rem(15);
		}

		.right-main-menu {
			margin-left: rem(15);
		}
	}
	@include media-breakpoint-up(xl) {
		.header-inner {
			.navbar-expand-lg  {
				.navbar-nav {
					> .menu-item {
						&:not(:last-child) {
							margin-right: rem(35);
						}
					}
				}
			}
		}
		.site-nav-container {
			width: calc(100% - 269px);
		}
		.left-main-menu {
			margin-right: rem(32);
		}

		.right-main-menu {
			margin-left: 0;
		}
	}
}